.app-wrapper__news .container-news {
    margin-top: 25px;
    margin-bottom: 100px;
    padding-bottom: 100px;
}

.app-wrapper__news .container-news .contentPage .news__item__title {
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 1.78px;
    text-transform: uppercase;
    margin-bottom: 5px;
}

.app-wrapper__news .container-news .contentPage .news__short__text,
.app-wrapper__news .container-news .contentPage .news__date {
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1.33px;
    margin-bottom: 10px;
}

.app-wrapper__news .container-news .contentPage .news__date {
    margin-top: 10px;
}

.app-wrapper__news .container-news .contentPage img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

//News Slide
.app-wrapper__news .container-news .contentPage .slider__news {
    margin-bottom: 110px;
}

.app-wrapper__news .container-news .contentPage .slider__news .slider__label {
    color: #4a4a4a;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 2.67px;
    text-transform: uppercase;
    padding-bottom: 25px;
}

.app-wrapper__news .container-news .contentPage .slider__content::after {
    content: '';
    position: absolute;
    bottom: -60px;
    width: 100%;
    height: 1px;
    background-color: #979797;
    left: 0;
}

.app-wrapper__news .container-news .contentPage .slider__content .slick-dots {
    width: auto;
    right: 0;
}

.app-wrapper__news
    .container-news
    .contentPage
    .slider__content
    .slick-dots
    li {
    width: 8px;
    height: 8px;
    border-radius: 100%;
    background-color: #d8d8d8;
}

.app-wrapper__news
    .container-news
    .contentPage
    .slider__content
    .slick-dots
    li.slick-active {
    background-color: #3a5cac;
}

.app-wrapper__news
    .container-news
    .contentPage
    .slider__content
    .slick-dots
    li
    button::before {
    opacity: 0;
}

.app-wrapper__news .container-news .contentPage .slider__content .slick-prev {
    z-index: -1;
    left: 0;
}

.app-wrapper__news .container-news .contentPage .slider__content .slick-next {
    right: 0;
    width: 6.4%;
    height: 100%;
}

.app-wrapper__news .container-news .contentPage .slider__content .slick-next::before {
    content: '';
}

.app-wrapper__news .container-news .contentPage .slider__item {
    display: flex;
    margin-right: 20px;
    width: 850px;
    position: relative;
    cursor: pointer;
}

.app-wrapper__news .container-news .contentPage .slider__item:focus {
    outline: none;
}

.app-wrapper__news
    .container-news
    .contentPage
    .slider__item
    .slider__item__image {
    flex-basis: 360px;
    padding-right: 10px;
    height: 360px;
}

.app-wrapper__news
    .container-news
    .contentPage
    .slider__item
    .slider__item__text {
    line-height: 22px;
    color: #4a4a4a;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    flex-basis: 50%;
    padding-left: 10px;
}

.app-wrapper__news
    .container-news
    .contentPage
    .slider__item
    .slider__item__text
    > .news__date {
        margin-bottom: 0;
    }

.app-wrapper__news
    .container-news
    .contentPage
    .slider__item
    .slider__item__text
    .slider__item__text-title {
    width: 80%;
}

.app-wrapper__news
    .container-news
    .contentPage
    .slider__item
    .slider__item__text
    .slider__item__text-short-text {
        height: 70px;
    }

//List News
.app-wrapper__news .container-news .contentPage .wrapper__listNews {
    display: flex;
    flex-wrap: wrap;
}

.app-wrapper__news .container-news .contentPage .wrapper__listNews .news-item {
    flex: 0;
    flex-basis: 50%;
    padding-bottom: 40px;
    line-height: 22px;
    position: relative;
}

.app-wrapper__news .container-news .contentPage .news-link {
    color: #4a4a4a;
}

.app-wrapper__news .container-news .contentPage .news-link:hover {
    text-decoration: none;
}

.app-wrapper__news .container-news .contentPage .news-link:focus {
    outline: none;
}

.app-wrapper__news
    .container-news
    .contentPage
    .wrapper__listNews
    .news-item
    .news__item__wrapper {
    width: 100%;
    display: flex;
    max-height: 160px;
}

.app-wrapper__news
    .container-news
    .contentPage
    .wrapper__listNews
    .news-item
    .news__item__wrapper
    .news__img {
    flex-basis: 160px;
    height: 160px;
}

.app-wrapper__news
    .container-news
    .contentPage
    .wrapper__listNews
    .news-item:nth-child(odd) {
    padding-right: 20px;
}

.app-wrapper__news
    .container-news
    .contentPage
    .wrapper__listNews
    .news-item:nth-child(even) {
    padding-left: 20px;
}

.app-wrapper__news
    .container-news
    .contentPage
    .wrapper__listNews
    .news-item
    .news__info {
    padding-right: 15px;
    flex: 1;
    flex-basis: calc(100% - 175px);
}

.app-wrapper__news
    .container-news
    .contentPage
    .wrapper__listNews
    .news-item
    .info__description {
    height: calc(100% - 30px);
}

.app-wrapper__news
    .container-news
    .contentPage
    .wrapper__listNews
    .news-item
    .info__description
    .info__description__text {
        height: 90%;
    }

.container-courseList .contentPage .courseList-pagination {
    padding-top: 15px;
}

@media (max-width: 1024px) {
    .app-wrapper__news .breadcrumb {
        padding: 0 20px;
        width: auto;
        margin-bottom: 0;
    }

    .app-wrapper__news .container-news {
        width: auto;
        margin-top: 0;
        margin-bottom: 0;
    }

    .app-wrapper__news
        .container-news
        .contentPage
        .slider__news
        .slider__label {
        padding-bottom: 10px;
    }

    // News Slide
    .app-wrapper__news .container-news .contentPage .slider__news {
        margin-bottom: 85px;
    }

    .app-wrapper__news .container-news .contentPage .slider__content::after {
        bottom: -45px;
    }

    .app-wrapper__news
        .container-news
        .contentPage
        .slider__content
        .slick-dots {
        width: 100%;
    }

    .app-wrapper__news .container-news .contentPage .slider__item {
        display: block;
        margin-right: 0;
    }

    .app-wrapper__news
        .container-news
        .contentPage
        .slider__item
        .slider__item__image {
        padding: 0;
        text-align: center;
        height: 200px;
        margin-bottom: 15px;
    }

    .app-wrapper__news
        .container-news
        .contentPage
        .slider__item
        .slider__item__text {
        padding: 0;
    }

    .app-wrapper__news
        .container-news
        .contentPage
        .slider__item
        .slider__item__text
        .slider__item__text-title {
        width: auto;
        margin: 10px 0;
    }

    .app-wrapper__news
    .container-news
    .contentPage
    .slider__item
    .slider__item__text
    .slider__item__text-short-text {
        height: auto;
    }

    //News list
    .app-wrapper__news
        .container-news
        .contentPage
        .wrapper__listNews
        .news-item {
        flex-basis: 100%;
        padding-bottom: 40px;
        margin-bottom: 40px;
        border-bottom: 1px solid #979797;
    }

    .app-wrapper__news
        .container-news
        .contentPage
        .wrapper__listNews
        .news-item:last-child {
        border: 0;
    }

    .app-wrapper__news
        .container-news
        .contentPage
        .wrapper__listNews
        .news-item:nth-child(odd) {
        padding-right: 0;
    }

    .app-wrapper__news
        .container-news
        .contentPage
        .wrapper__listNews
        .news-item:nth-child(even) {
        padding-left: 0;
    }

    .app-wrapper__news
        .container-news
        .contentPage
        .wrapper__listNews
        .news-item
        .news__item__wrapper
        .news__info {
        padding-right: 15px;
        flex-basis: 70%;
    }

    .app-wrapper__news
    .container-news
    .contentPage
    .slider__item
    .slider__item__text {
        display: block;
    }
}
