.app-wrapper__enrolLanding {
    padding: 0 20px;
}

.container-landingPage {
    width: 970px;
    margin: auto;
    padding-bottom: 80px;
}

.container-landingPage .landingPage {
    margin-bottom: 20px;
    text-align: center;
}

.container-landingPage .landingPage .landingPage__date {
    color: #4a4a4a;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 1.78px;
    text-transform: uppercase;
    padding: 12px 0;
}

.contentPage .section {
    padding-bottom: 25px;
}

.contentPage .section ul,
ol {
    padding-left: 20px;
}

.contentPage .section ul li {
    color: #4a4a4a;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1.33px;
    line-height: 22px;
}

.contentPage .section ol li {
    color: #4a4a4a;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1.33px;
    line-height: 22px;
}

.contentPage .section .section__lable {
    color: #4a4a4a;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 1.78px;
    text-transform: uppercase;
    padding: 5px 0;
}

.contentPage .section .section__description,
.section__listSkills {
    color: #4a4a4a;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1.33px;
    line-height: 22px;
}

.section__description ul {    
    padding-left: 12px;
}

.contentPage .section .section__content {
    color: #4a4a4a;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1.33px;
    line-height: 22px;
    padding-bottom: 30px;
}

.contentPage .btn {
    text-align: center;
    padding-top: 15px;
}

.contentPage .btn button {
    height: 60px;
    border-radius: 30px;
    background-color: #3a5cac;
    color: #ffffff;
    font-size: 20px;
    font-weight: 700;
    width: 388px;
    letter-spacing: 2.22px;
    text-transform: uppercase;
}

@media (max-width: 1024px) {
    .container-landingPage {
        width: 100%;
        padding-bottom: 60px;
    }

    .contentPage .btn {
        padding-top: 30px;
    }

    .contentPage .btn button {
        height: 48px;
        border-radius: 24px;
        width: 280px;
        font-size: 16px;
        letter-spacing: 1.78px;
    }
}