.app-wrapper__about {
    padding: 0 20px;
}

.app-wrapper__about .container-about {
    width: 760px;
    margin: auto;
    padding-bottom: 50px;
}

.app-wrapper__about .container-about .about__title {
    letter-spacing: 2.03px;
    line-height: 52px;
    padding-bottom: 70px;
}

.app-wrapper__about .container-about .contentPage .section__lable {
    color: #4a4a4a;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 1.78px;
    text-transform: uppercase;
    padding-bottom: 3px;
}

.app-wrapper__about .container-about .contentPage .section__description {
    color: #4a4a4a;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1.33px;
    line-height: 22px;
}

@media (max-width: 1024px) {
    .app-wrapper__about .container-about {
        width: auto;
        padding-top: 0;
    }

    .app-wrapper__about .container-about .about__title {
        font-size: 24px;
        letter-spacing: 0.62px;
        line-height: 28px;
    }
}
