.success__checkmark {
    height: 45px;
    width: 45px;
    margin: 0 auto;
    background-repeat: no-repeat;
    background-image: url('../assets/images/attend.svg');
    background-size: cover;
    background-position: center center;
}

@media (min-width: 1024px) {
    .success__checkmark {
        height: 60px;
        width: 60px;
    }
}