.news-pagination {
    margin-top: 20px;
}

.pagination__wrapper {
    display: flex;
    width: 100%;
    align-items: center;
}

.page-link {
    font-size: 12px;
    color: #4a4a4a;
    margin: 0 5px;
    text-decoration: none;
    user-select: none;
    cursor: pointer;
}

.page-item{
    background-color: #efefef ;
}

.page-items .page-link {
    font-size: 25px;
    color: #4a4a4a;    
    text-decoration: none;
    user-select: none;
    cursor: pointer;
    margin: -2px;
}

li.page-item.active{
    background-color: #3a5cac;    
    font-weight: 700;
}
li.page-item.active a{
    color: #fff;
}

.pagination__wrapper .page-link.disabled {
    cursor: default;
    opacity: 0.5;
}

.page-link{
    font-size: 25px;
    color: #4a4a4a;
    margin: 0 5px;
    text-decoration: none;
    user-select: none;
    cursor: pointer;
}

.page-link:focus {
    outline: none;
}

.page-link:hover {
    opacity: 0.8;
    text-decoration: none;
}

.page-link:visited {
    color: #4a4a4a;
}

.pagination {
    display: flex;
}

.pagination li {
    display: inline-block;
    margin: 0 5px;
    color: #4a4a4a;
    text-align: center;    
}

.pagination li a{        
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1.33px;
    line-height: 28px;    
    user-select: none;
}

.page-item {
    width: 28px;
    height: 28px;
    border-radius: 4px;
    cursor: pointer;
    background-color: #efefef;
}

@media (max-width: 767px) {
    .pagination__wrapper {
        justify-content: center;
    }
}